import React, { useState } from 'react';
import { css } from '@emotion/react';

import DefalutLayout from '../layouts/default';
import Header from '../components/white-header';

import arrowIcon from '../images/black-back-arrow.png';
import homeIcon from '../images/black-home.png';
import { Navigate, useNavigate } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import styled from '@emotion/styled';
import DefaultDialog from '../components/default-dialog';
import InfoDialog from '../components/info-dialog';
import axios from 'axios';
import { useAxios } from '../context/axios';

const Label = styled.div`
    color: #12183F;

    font-family: 'festivalfont-80';
    font-size: 1.1rem;
`

const Input = styled.input`
    border: 0;
    padding: 0;

    box-sizing: border-box;

    width: stretch;

    background-color: transparent;

    font-size: 1rem;
    font-family: 'festivalfont-40';
    color: #12183F;

    padding-bottom: 12px;

    border-bottom: 1px solid #D3D3D9;

    ::placeholder {
        font-size: 1rem;
        color: #C0C0C6;
    }
    :focus {
        outline: none;
        border-bottom: 1px solid #12183F;
    }
`

export default function SignUpPage() {
    const navigate = useNavigate();
    const { register, handleSubmit } = useForm();
    const [dialogOpen, setDialogOpen] = useState(false);
    const [infoMessage, setInfoMessage] = useState('');

    const axios = useAxios();

    return(
        <div
            css={css`
                background-color: white;
                width: 100vw;
                height: 100vh;
            `}
        >
            <DefalutLayout>
                <div
                    //   
                    css={css`
                        height: 48px;
                    `}
                />
                <Header
                    title={''}
                    leftIcon={
                        {
                            iconImage: arrowIcon,
                            onClick: () => {
                                navigate(-1);
                            }
                        }
                    }
                    rightIcons={[
                        {
                            iconImage: homeIcon,
                            onClick: () => {
                                navigate('/');
                            }
                        },
                        
                    ]}
                />
                <div
                    //  
                    css={css`
                        margin: 0px 20px;
                    `}
                >
                    <div
                        //  
                        css={css`
                            font-family: 'festivalfont-80';
                            font-size: 1.4rem;

                            margin-top: 30px;

                            color: #12183F;
                        `}
                    >
                        , 
                    </div>
                    <div
                        //  
                        css={css`
                            font-size: 0.9rem;

                            margin-top: 8px;
                            color: #767687;
                        `}
                    >
                           !
                    </div>

                    <form
                        // 
                        onSubmit={handleSubmit((data) => {
                            console.log(data);
                            if(data.password !== data.passwordCheck){
                                console.log('');
                                setInfoMessage(' .');
                                setDialogOpen(true);
                                return;
                            }
                            
                            axios.get(`/users?username=${data.username}&nickname=${data.nickname}`
                                ).then((response) => {                 
                                    if(!response.data.username){
                                        setInfoMessage('  .');
                                        setDialogOpen(true);
                                        return;
                                    }
                                    if(!response.data.nickname){
                                        setInfoMessage('  .');
                                        setDialogOpen(true);
                                        return;
                                    }
                                    
                                    axios.post(`/users`, data).then((response2) => {
                                        navigate('/');
                                    });
                                }).catch((error) => {
                                    console.log('');
                                })
                              

                            console.log('');
                        })}
                    >
                        <Label css={css`margin-top: 48px;`}>
                            
                        </Label>
                        <Input css={css`margin-top: 16px;`}
                            // 
                            {...register("nickname", {
                                required: " .",
                                minLength: {
                                    value: 2,
                                    message: "  2"
                                },
                                maxLength: {
                                    value: 15,
                                    message: "  15"
                                }
                            })}
                            placeholder="   ?"
                        />
                        
                        <Label css={css`margin-top: 32px;`}>
                            
                        </Label>
                        <Input css={css`margin-top: 16px;`}
                            // 
                            {...register("username", {
                                required: " .",
                                minLength: {
                                    value: 2,
                                    message: "  2"
                                },
                                maxLength: {
                                    value: 15,
                                    message: "  15"
                                }
                            })}
                            placeholder=" ."
                        />

                        <Label css={css`margin-top: 32px;`}>
                            
                        </Label>
                        <Input css={css`margin-top: 16px;`}
                            // 
                            type="password"
                            {...register("password", {
                                required: "  .",
                                minLength: {
                                    value: 2,
                                    message: "  2"
                                },
                                maxLength: {
                                    value: 15,
                                    message: "  15"
                                }
                            })}
                            placeholder="  ."
                        />

                        <Label css={css`margin-top: 32px;`}>
                             
                        </Label>
                        <Input css={css`margin-top: 16px;`}
                            // 
                            type="password"
                            {...register("passwordCheck", {
                                required: "  .",
                                minLength: {
                                    value: 2,
                                    message: "  2"
                                },
                                maxLength: {
                                    value: 15,
                                    message: "  15"
                                }
                            })}
                            placeholder="  ."
                        />

                        <button
                            //  
                            type='submit'
                            css={css`
                                position: fixed;
                                bottom: 40px;

                                display: flex;

                                align-items: center;
                                justify-content: center;

                                border: 0;

                                padding: 16px auto;
                                left: 20px;
                                right: 20px;

                                border-radius: 8px;

                                width: stretch;
                                height: 50px;

                                background-color: #12183F;

                                color: white;
                                font-size: 1rem;
                            `}
                        >
                             
                        </button>
                    </form>

                </div>
                
                {dialogOpen &&
                <div
                    // blur div
                    css={css`
                        position: fixed;

                        top: 0px;
                        left: 0px;

                        width: 100vw;
                        height: 100vh;

                        backdrop-filter: blur(10px);
                    `}
                >

                </div>
            }
                {dialogOpen && 
                <InfoDialog
                    setDialogOpen={setDialogOpen}
                    infoText={infoMessage}
                    functions={[
                        {
                            name: '',
                            color: '#12183f',
                            onClick: () => {
                                setDialogOpen(false);
                            }
                        },
                    ]}
                />
            }
            </DefalutLayout>
        </div>
    );
}