import React from 'react';
import { css, useTheme } from '@emotion/react';
import PropTypes from 'prop-types';





export default function Header({ title, leftIcon, rightIcons}){
    const theme = useTheme();

    return(
        
        <div 
            //  
            css={css`
                display: flex;
                position: fixed;
                
                top: 0px;
                align-items: center;

                padding: 0px 22px;
                width: stretch;
                height: 48px;
                
                background-color: ${theme.colors.white};
                
        `}>
            {leftIcon !== null ?
            <button
                //  
                type='button'
                onClick={leftIcon?.onClick}
                css={css`
                    background-color: transparent;
                    
                    
                    border: 0;
                    padding: 0;
                `}
            >
                <img src={leftIcon?.iconImage}
                    css={css`
                    object-fit: cover;
                `}/>
            </button> : null}
            
            {rightIcons !== null ?
            
            <button
                // 2 
                type='button'
                onClick={rightIcons[1]?.onClick}
                css={css`
                    margin-left: auto;
                    background-color: transparent;
                    border: 0;
                    padding: 0;
                `}
            >
                <img
                    src={rightIcons[1]?.iconImage}
                    css={css`
                        object-fit: cover;
                `}/>
            </button>

            : null
            }
            
            {rightIcons !== null ?  
            <button
            // 1  ()
            type='button'
            onClick={rightIcons[0]?.onClick}
            css={css`
                margin-left: 30px;
                background-color: transparent;
                border: 0;
                padding: 0;
            `}>
            <img 
                src={rightIcons[0]?.iconImage}
                css={css`
                    align-self: flex-end;
                    object-fit: cover;
            `}/>
            </button> : null }

            <div
                //  
                css={css`
                    position: absolute;
                    left: 50%;
                    transform: translate(-50%);

                    font-weight: 400;
                    color: ${theme.colors.primary1};
                    font-size: 1.4rem;
                `}>
                {title}
            </div>

            
        
        </div>
    );
}


Header.propTypes = {
    title: PropTypes.string,
    backActivated: PropTypes.bool,
    leftIcon: PropTypes.object,
    rightIcons: PropTypes.array,
};

Header.defaultProps = {
    title: 'Default',
    backActivated: false,
    leftIcon: null,
    rightIcons: null,
};