import React, { useState } from 'react';
import { css } from '@emotion/react';

import DefalutLayout from '../layouts/default';
import Header from '../components/header';

import arrowIcon from '../images/back-arrow.png';
import mapIcon from '../images/map.png';
import FoodList from '../components/ham-square/food-list-ham';
import { useNavigate } from 'react-router-dom';
import DefaultModalMap from '../components/default-modal-map';
import modalImage from '../images/modal_map_hamsquare.png';

export default function HamSquareListViewPage() {
    const navigate = useNavigate();
    const [dialogOpen, setDialogOpen] = useState(false);
    const showDialog = () => {
        setDialogOpen(true);
    };

    return(
        <DefalutLayout>
            <div
                //   
                css={css`
                    height: 48px;
                `}
            />
            <Header 
                title={''}
                leftIcon={
                    {
                        iconImage: arrowIcon,
                        onClick: () => {
                            navigate(-1);
                        }
                    }
                }
                rightIcons={[
                    {
                        iconImage: mapIcon,
                        onClick: () => {
                            showDialog();
                        }
                    },
                ]}
            />

            <FoodList />
            
            {dialogOpen &&
                <div
                    // blur div
                    css={css`
                        position: fixed;

                        top: 0px;
                        left: 0px;

                        width: 100vw;
                        height: 100vh;

                        backdrop-filter: blur(10px);
                    `}
                >

                </div>
            }
            {dialogOpen && 
                <DefaultModalMap
                    setDialogOpen={setDialogOpen}
                    image={modalImage}
                />
            }
        </DefalutLayout>
    );
}